.bg-myc {
  background-color: #0f063d;
}

.dp-img {
  height: 200px;
  width: 175px;
}

.score {
  position: absolute;
  top: 2rem;
}

.my-model {
  height: 100vh;
  width: 100%;
  display: flex;
  align-content: center;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5000;
  background-color: rgba(0,0,0,0.3);
  overflow: scroll;
}

.my-tag {
  width: 30%;
}

.my-input {
  width: 70%;
}

.model-body {
  width: 50%;
  background-color: white;
  border-width: 1rem;
  border-color: #0f063d;
}

.my-tab {
  width: 75%;
}

.mt-lg {
  margin-top: 400px;
}

@media only screen and (max-width: 768px) {
  .model-body {
    width: 100%;
  }

  .my-tab {
    width: 90%;
  }
  
  .my-tag {
    width: 100%;
  }

  .my-input {
    width: 100%;
  }
}

.flex-input {
  min-width: 5rem;
}

.input-border {
  box-sizing: border-box;
  border-color: #0f063d;
}

.input-border-danger {
  box-sizing: border-box;
  border-color: red;
}

.box-sm {
  width: 1rem;
  height: 1.5rem;
  text-align: center;
}

.text-myc {
  color: #0f063d;
}

.text-myc-bold {
  color: #0f063d;
  font-weight: 600;
}

.text-bold {
  font-weight: 600;
}

.my-text-lg {
  font-size: 1.5rem;
}

.my-border {
  border-style: solid;
  border-width: 0.1rem;
}

.my-btn {
  color: #0f063d;
  background-color: transparent;
  border-width: 0.1rem;
  border-color: #0f063d;
  font-weight: 600;
}

.my-btn:hover {
  color: white;
  background-color: #0f063d;
}

.border-myc {
  border-color: #0f063d;
}

.ex-icon {
  top: 1rem;
  padding-left: 1rem;
}

.screen-center {
  position: relative;
  top: 40vh;
}

.my-hr {
  margin: 0rem;
  margin-bottom: 1rem;
  border-top: 2px solid;
}

.app {
  background-color: white;
  height: 100vh;
}

.float-right{
  position: absolute;
  right: 0;
  top: 0;
}

.dropdown { 
  position:static !important; 
}

.document{
  width: 100%;
  height: 600px;
  border: 2px solid #ccc;
}

.dropdown-menu { 
  box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .15)!important; 
  margin-top:0px !important; 
  width:100% !important; 
} 

.nav-pills .nav-link.active, .nav-pills .show > .nav-link {
  background-color: #0f063d;
}

a {
  color: #0f063d;
}


/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance:textfield;
}